@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
*{box-sizing:border-box;margin:0;padding:0}body{font-family:'Roboto Mono', sans-serif;background-color:#1d1d21}button{cursor:pointer;border:none;outline:none}button:disabled{cursor:default}a{text-decoration:none;color:inherit}.MuiFilledInput-inputMultiline::-webkit-scrollbar{width:6px}.MuiFilledInput-inputMultiline::-webkit-scrollbar-track{background-color:#1d726b;border-radius:4px}.MuiFilledInput-inputMultiline::-webkit-scrollbar-thumb{background:#144944;border-radius:4px}@-webkit-keyframes spin{to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spin{to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.homepage{background-color:#131313;height:100vh;display:flex;justify-content:center;align-items:center;color:#fff}.homepage__container{position:absolute}.homepage__container header{font-size:92px;font-weight:600;text-transform:uppercase;text-align:center}.homepage__buttons{margin-top:80px;display:flex;justify-content:space-around;align-items:center}.homepage__buttons .btn{display:flex;justify-content:center;align-items:center;outline:none;background-color:#40378b;box-shadow:rgba(255,255,255,0.1) 0px 0px 18px;border:none;height:70px;width:25%;border-radius:10px;font-size:32px;color:rgba(255,255,255,0.8);text-align:center;-webkit-transition:background-color 0.15s linear;transition:background-color 0.15s linear}.homepage__buttons .btn:hover{background-color:#514991;color:white}.homepage__buttons .btn p{line-height:35px}#back-vid{position:absolute;width:100%;height:100vh;object-fit:cover;opacity:0.4}@media screen and (max-width: 1000px){.homepage__container header{font-size:64px}.homepage__buttons .btn{height:120px}}@media screen and (max-width: 530px){.homepage__container header{font-size:40px}.homepage__buttons .btn{width:40%;font-size:24px}}

nav{position:absolute;width:100%;background-color:rgba(15,15,15,0.85);height:60px;display:flex;justify-content:flex-end;align-items:center;color:white;font-weight:500;font-size:24px;color:#545454;z-index:1000}.nav__account{display:flex;justify-content:space-between;align-items:center;width:300px;margin-right:15px}.nav__account div{cursor:pointer;-webkit-transition:color 0.15s linear;transition:color 0.15s linear}.nav__account div:hover{color:#ffffff}@media only screen and (max-width: 500px){.nav__account{margin:auto}}

.loading-screen{width:100%;display:flex;justify-content:center;flex-direction:column;align-items:center}.spin{width:100px;height:100px;border:4px solid rgba(255,255,255,0.3);border-radius:50%;border-top-color:#fff;-webkit-animation:spin 1s ease-in-out infinite;animation:spin 1s ease-in-out infinite}

.error{height:16px;font-size:12px;margin-bottom:4px;width:100%;color:#c97373}

.form-container{background-color:#1d1d21;height:100vh;display:flex;justify-content:center;align-items:center;color:#fff}.form-box{width:1000px;background-color:#2a2a31;display:flex;align-items:center;flex-direction:column;text-align:center;padding:0px 100px}.form-box h1{font-size:42px;margin-top:10px}.form-box p{font-size:14;letter-spacing:-1px;color:#a4a4a4}.form-box p span{text-decoration:underline;color:#1bdfcb}.form-box h2{font-size:21px;color:#a4a4a4;margin-top:25px}.input-section{display:grid;grid-template-columns:1fr 1fr;grid-template-rows:auto;grid-gap:4px}.submit-button{height:50px;width:20%;background-color:#2ba296;border-radius:10px;outline:none;display:flex;align-items:center;justify-content:center;font-size:18px;border:none;margin:30px auto;color:inherit;cursor:pointer;-webkit-transition:background-color, width 0.3s ease-in-out;transition:background-color, width 0.3s ease-in-out}.submit-button:disabled{background-color:#15554f}.submit-button:hover{background-color:#32b8aa;width:30%}.submit-button:hover:disabled{background-color:#15554f}.submit-button span{margin-right:10px;line-height:20px}.form{width:100%;display:flex;justify-content:center;flex-direction:column}.form-error{width:60%;background-color:#c97373;border-radius:6px;margin:12px auto 0 auto;text-align:center;padding:8px;-webkit-animation:fade-in 0.3s linear forwards;animation:fade-in 0.3s linear forwards}@-webkit-keyframes fade-in{from{opacity:0}to{opacity:1}}@keyframes fade-in{from{opacity:0}to{opacity:1}}@media only screen and (max-width: 700px){.input-section{grid-template-columns:1fr}.submit-button{width:60%}}@media only screen and (max-width: 500px){.form-box{padding:0px 30px;width:100%}.form-container{height:auto;padding-top:60px}}

.comments{margin-top:25px;color:#656682}.comments h4{font-size:20px}.comments_block{margin-top:10px;padding:10px 20px 20px 20px;border-top:#5a5d7d 1px solid;display:flex;flex-direction:column;max-height:200px;overflow-y:auto}.comments_block::-webkit-scrollbar{width:10px}.comments_block::-webkit-scrollbar-track{background-color:rgba(100,84,143,0.2);border-radius:10px;margin-top:6px}.comments_block::-webkit-scrollbar-thumb{background:rgba(144,121,207,0.2);border-radius:10px}.comments_block::-webkit-scrollbar-thumb:hover{background:rgba(177,158,228,0.2)}.comment{margin-top:10px;display:flex}.comment_avatar{background-color:#3a3a44;width:80px;height:80px;margin-top:6px}.comment-content{width:950px;margin-left:20px}.comment-content h5{margin-top:4px;font-size:18px}.comment-content p{font-size:16px;color:#f6f2f2}.comment-submit,.comment-submit:disabled{background-color:#279388;display:inline-block;width:80px;height:30px;border-radius:6px;color:white;font-family:'Roboto Mono';font-size:14px}.comment-submit:hover{background-color:#35beb1}.comment-submit:disabled{background-color:#075e55;color:#0b8a7d}.no-comments{font-size:24px}

.not_found_container{height:100vh;color:white;display:flex;justify-content:center;align-items:center;flex-direction:column}.not_found_error{font-size:42px}.not_found_go_home{font-size:20px;cursor:pointer;text-decoration:underline;color:lightblue}

.eventPage-container{padding-top:90px;display:flex;justify-content:center;align-items:center;height:100vh}.eventPage-box{padding:30px 80px;background-color:#2a2a31;width:1340px;color:#f6f2f2;font-size:20px}.box_head{display:flex;justify-content:space-between;align-items:flex-start}.head_title-creator{margin-top:-12px;text-align:center}.head_title-creator h2{font-size:40px}.box_description{margin-top:20px;color:#888888;text-align:center}.box_enroll-button{margin:30px auto 0px auto}

